.navBar{
    background-color: #CB3737;
    font-size: 1.1rem;
    color: white;
    box-shadow: -1px 9px 32px -4px rgba(0,0,0,0.75);
    box-shadow: -1px 9px 32px -4px rgba(0,0,0,0.75);
    box-shadow: -1px 9px 32px -4px rgba(0,0,0,0.75);
}

.nav-link{
    color: white;
}

.nav-link:hover{
    color: #E3E3E3;
    font-size: 1.07rem;
}

.logoNavBar{
    width: 100%;
}

.navbar-brand{
    width: 7%;
    margin-right: 2rem;
}

@media (max-width: 992px){
    .nav-link{
        text-align: center;
        margin: 0rem;
        padding: 0rem;
    }
    
    .botonNavBar{
        padding: 0rem;
        margin-bottom: 0.5rem;
    }
}

@media (min-width: 992px){
    .botonNavBar{
        padding-left: 0rem;
    }
}