@import url('https://fonts.googleapis.com/css2?family=Ubuntu&display=swap');

.body{
    font-family: 'Ubuntu', sans-serif;
}

.fondoGame{
    background-color: #413F42;
    width: 100vw;
    height: 100%;
    padding-bottom: 10rem;
}

.volverInicio {
    font-size: 35px;
    padding-top: 1.5rem;
}

.inicio{
    color: #CB3737;
    font-family: 'Ubuntu', sans-serif;
}

.inicio:hover{
    color: #EE6F57;
}

.tituloGame{
    border-bottom: 3px solid #CB3737;
    color: white;
    width: 60%;
}

.fichaTecnicaTexto{
    border-bottom: 3px solid #CB3737;
    padding-left: 1rem;
    padding-bottom: 0.3rem;
    width: 90%;
    margin: auto;
    box-shadow: 0px 10px 0px -5px rgba(0,0,0,0.75);

}

.fixedBottom {
    position: fixed;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1030;
}

.btn-darkk {
    --bs-btn-color: #fff;
    --bs-btn-bg: #CB3737;
    --bs-btn-border-color: #E3E3E3;
    --bs-btn-hover-color: #fff;
    --bs-btn-hover-bg: #EE6F57;
    --bs-btn-hover-border-color: #EE6F57;
    --bs-btn-focus-shadow-rgb: 66,70,73;
    --bs-btn-active-color: #fff;
    --bs-btn-active-bg: #EE6F57;
    --bs-btn-active-border-color: #EE6F57;
    --bs-btn-active-shadow: inset 0 3px 5px rgba(0, 0, 0, 0.125);
    --bs-btn-disabled-color: #fff;
    --bs-btn-disabled-bg: #2B2E4A;
    --bs-btn-disabled-border-color: #2B2E4A;
}

.descripcionTexto{
    text-align: justify;
}

@media (max-width: 992px){
    
    .divFichaTecnica{
        margin-top: 4rem;
    }

    .containerCarouselGame{
        min-height: 40vw;
        max-height: 40vw;
        width: 80%;
        margin: auto;
        margin-top: 3rem;
    }

    .imgCarouselGame{
        min-height: 40vw;
        max-height: 40vw;
        width: auto;
    }

    .cardComentario{
        color: black;
        margin-top: 3rem;
        background-color: white;
        box-shadow: -10px -10px 10px 0px rgba(0,0,0,0.75);
        height: fit-content;
        padding: 2rem;
        width: 100%;
    }
}

@media (min-width: 992px){

    .divFichaTecnica{
        margin-top: 4rem;
        margin-left: 3rem;
    }

    .containerCarouselGame{
        min-height: 25vw;
        max-height: 25vw;
        width: 50%;
        margin: auto;
        margin-top: 3rem;
    }

    .imgCarouselGame{
        min-height: 25vw;
        max-height: 25vw;
        width: auto;
    }

    .cardComentario{
        color: black;
        margin-top: 3rem;
        background-color: white;
        box-shadow: -10px -10px 10px 0px rgba(0,0,0,0.75);
        height: fit-content;
        padding: 2rem;
        width: 75%;
    }
}

@media (min-width:560px) and (max-width: 992px){
    .cardComentario{
        color: black;
        margin-top: 3rem;
        background-color: white;
        box-shadow: -10px -10px 10px 0px rgba(0,0,0,0.75);
        height: fit-content;
        padding: 2rem;
        width: 100%;
    }
}